import React from 'react'
import {NavLink} from 'react-router-dom'

const About = () => {
  return (
  <div className="jumbotron">
    <h1 className="display-4">About App</h1>
    <p className="lead">Application version <strong>1.0.0</strong></p>
    <hr className="my-4"></hr>
    <p>That app help to find interesting stream online from Twitch.</p>
    <NavLink className="btn btn-light btn-lg" exact to="/" role="button">Return to home page</NavLink>
  </div>
  )
}

export default About