import React from 'react'

const StreamsItems = (props) => {
  const {url, name, game, status, language} = props
  return (
      <tr>
      <th scope="row"><a href={url} target="_blank" rel="noopener noreferrer">{name}</a></th>
        <td>{game}</td>
        <td>{status}</td>
        <td>{language}</td>
      </tr>
  )
}

export default StreamsItems