import React, { useState, useEffect, useContext  } from 'react'
import StreamsTable from '../components/StreamsTable'
import documents from '../assets/documents.svg'
import { AlertContext } from '../context/alert/alertContext';

const Home = () => {
  const {show} = useContext(AlertContext)
  const [ streams, setStreams ] = useState([{name: "No data", game: "No data", status: "No data"}])
  // eslint-disable-next-line

  const languageSort = (arr) => {
    const ruArr = []
    const enArr = [] 
    const newArr = []
    arr.forEach(item => {
      (item.channel.language === 'ru') ? ruArr.push(item) : enArr.push(item)
    })

    return newArr.concat(ruArr, enArr)
  }

  const sortForCopy = (arr) => {
    let str = ''
    arr.forEach(item => {
      const { url, game } = item.channel
      str += `${url} ${game} \n`
    })
    return str
  }

  const getResponse = async() => {
    const url = 'https://api.twitch.tv/kraken/streams/followed'
    try {
      const res = await fetch(url, {
      headers: {
        "Accept": "application/vnd.twitchtv.v5+json",
        "Client-ID": "n90nl8vm88jmdjy25rr1f4sh99s547",
        "Authorization": "OAuth 35t29kpyvax4vrxwr8wsr1d6tmly3b"
      },
    })
      if (!res.ok) {
        if (res.status === 401) show(`Please, add authorization to Twitch!`)
        else show(`We have problems with connection to Twitch... Error ${res.status}`)
        throw new Error(`Could not fetch ${url}, received ${res.status}`)
      }
      await res.json()
        .then(data => {
          setStreams(languageSort(data.streams))
          })
    } catch (e) {
      console.log(e)
    }
  }

  useEffect(() => {
    getResponse()
    // eslint-disable-next-line
  }, [])

  const copy = () => navigator.clipboard.writeText(`${sortForCopy(streams)}`).catch(err => {
    console.log('Something went wrong', err);
  })


  return (
    <div>
      <div className="d-flex p-1 bd-highlight justify-content-between">
        <h5 className="p-2">Streams Online Right Now</h5>
        <button className="btn btn-light" onClick={() => copy()}>
        <img src={documents} alt="Documents" width="24" height="24" title="Documents" style={{opacity: "0.5"}}></img>
          <span> Copy list</span></button>
      </div>
      <p></p>
      <StreamsTable streams={streams} />
    </div>
  )
}

export default Home